	module.exports = {"data":{"markdownRemark":{"html":"<h3>Security@ Email Forwarding</h3>\n<p>We enable vulnerability emails sent to programs’ security@ emails to automatically be forwarded as a report in your HackerOne inbox.  </p>\n<h3>Custom Inbox Views</h3>\n<p>We now enable users to create and save their own custom View in their inbox. </p>\n<h3>Trigger for Low Bounty Balance</h3>\n<p>We now enable programs to set up a trigger for when their balance falls below a certain amount. </p>\n<h3>Inline Video Attachments</h3>\n<p>We now enable hackers to attach videos to their vulnerability reports. </p>","frontmatter":{"path":"/changelog/2017/january","title":"January 2017"}}},"pathContext":{}}

